import { mapGetters } from "vuex";
import { mapState } from "vuex";
export default {
  data() {
    return {
      tokenSrc: "", 
      flag: 0,
      selected: "",
      options: [],
      message: "",
      datetime1: "",
      langFlag : localStorage.LANGUAGE,
      dataList:[],
      memo: "",
    };
  },
  created: function() {
    let myDate = new Date();
    this.datetime1 = myDate.getHours() + " : " + myDate.getMinutes();
    let params = {
      category_id: this.$route.query.categoryId,
      status: 1,
      hotelid: 6
      //  hotelid: localStorage.HOTELID,
    };
    this.$store.dispatch("getTaskList", params).then(res => {
      let data = res.data;
      if (data.code == 0) {
        data = data.data.list;
        for (var item in data) {
          this.dataList.push(data[item]);
        }
      } else {
        this.$dialog.toast({ mes: res.msg, timeout: 1000 });
      }
    });
  },
  methods: {
      submit: function() {
       let time = "预约时间："+this.datetime1;
       let params = {
         memo : this.memo +" "+ time,
         token : localStorage.TOKEN, 
         task_id : this.selected,
       };
    this.$store.dispatch("addTask", params).then(res => {
      let data = res.data; 
      if (data.code == 0) {  
        this.$dialog.toast({ mes: "已成功提交！", timeout: 1000 });
      } else {
        this.$dialog.toast({ mes: res.msg, timeout: 1000 });
      }
    }); 

    },
    goBack: function() {
      this.$router.go(-1);
    }
  },
  mounted: function() {
    //一级页面falg
    isHomePage(0);
  },
  computed: {
    ...mapState({
      language: state => state.language.language
    })
  }
};



